@font-face {
  font-family:Poppins;
  src: url(./Poppins/Poppins-Light.ttf);
  font-weight: 300;
}
body{
  scroll-behavior: smooth;
}
.App{
  width: 100%;
  margin: 0;
  padding: 0;
  
}
.MenuWrapper{
    height: 80px;
    padding: 10px 25px;
    box-sizing: border-box;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    position: fixed;
    width: 100%;
    background: #fff;
    z-index: 1;

}

.MainBody{
  display: flex;
  margin: auto;
  padding: 30px;
  font-family: 'Poppins', sans-serif;
  line-height: 1.8;
  justify-content: space-evenly;
  width: 80%;
  
  
}
.IntroWrapper{
    min-height: 250px;
    background: #f5f5f5;
    padding-top: 80px;

}
.ImageHolder{
  flex-grow: 1;
  text-align: center;
  padding: 30px;
  
}
.ImageHolder img{
  width: 300px;
  border-radius: 50%;
}

.Info{
  flex-grow: 2;
  padding: 10px 30px;
  text-align: justify;
}

.Info a{
  text-decoration: none;
  color: #3697FF;
}

.Info span:first-child{
  color: #010101;
  font-size: 30px;
  font-weight: bold;
  
}
.Info span:nth-child(2){
  
  font-size: 30px;
  font-weight: bold;
}

.LinksWrapper{
  background-color: #fff;
  box-sizing: border-box;
}
.LinksWrapper img{
  width: 80%;
}
.LinksWrapper .LinkImgWrapp{
  flex-grow: 1;
  max-width: 200px;
  max-height: 200px;
  overflow: hidden;
}




.HighlightsWrapper{
  min-height: 360px;
  background-color: #f5f5f5;
  padding-bottom: 50px;
 
}

.HighlightsWrapper p:first-child{
  flex-grow: 1;
  color: #0c76e4;
  font-size: 30px;
  font-weight: bold;
  
}

.HighlightRow {
  min-height: 40px;
  flex-grow: 1;

  flex-direction: row;
}
.HighlightRow div{
  min-height: 20px;
  box-sizing: border-box;
}
.dates{
  font-size: 18px;
  font-weight: bold;
  width: 35px;
  text-align: left;
  display: inline-block;
  width: 8%;
}

.dashes{
  display: inline-block;
  text-align: center;
  width: 2px;
  display: none;

}
.dashes img{
  width: 25px;
  display: none;
  
}
.highlightTxt{
  box-sizing: border-box;
    color: #6b6969;
    font-weight: bold;
    display: inline-block;
}
.highlightTxt a{
  text-decoration: none;
  color: #0c76e4;

}

.dashed-line{
  border-right: 2px dashed #939393 !important;
  content: "";
  height: auto;
  top: -5px;
  width: 0;
  margin: auto;
  position: relative;
}

.PublicationsWrapper{
  background-image: url("./robots.jpg");
  min-height:350px;
  width: 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
 
}
.transparency{
  width: 100%;
  min-height: 350px;
  background-color: #fff;

}
.PublicationsWrapper p:first-child{
  flex-grow: 1;
  color: #010101;
  font-size: 42px;
  font-weight: bold;
}

.PublicationsWrapper .item{
  flex: 1 ;
  flex-basis: 80%;
  min-height: 100px;
  text-align: center;
  padding-bottom: 20px;
  display: flex;
  padding-top: 20px;
   
}

.PublicationsWrapper .item img{ 
   flex:1;
   width: 30px;
  
  
}

.title {
  font-weight: bold;
    color: #010101;
    flex: 3 1;
    font-size: 85%;
    text-align: left;
    padding-left: 20px;
}
.names{
  display: block;
  font-weight: 100;
  font-size: 14px;
  margin-top: 20px;
}
.PublicationsWrapper  .links{
  flex:1;
  height: 35px;
  display: inline-block;
  text-decoration: none;
  color: #010101;
  font-size: 12px;
 
}
.PublicationsWrapper  .links a{
  display: block;
  color: #3697FF;
  text-decoration: none;
  margin-bottom: 7px;
  text-align: center;
  font-weight: bold;
  font-size: 13px;
  
}
.PublicationsWrapper  .links a span{
  display: inline-block;
  position: relative;
  top: -8px;
  margin-left: 8px;
}

.PublicationsWrapper hr{
  width: 80%;
  border: 1px solid #e6e3e3;
}

.VideoWrapper{
  background: #f5f5f5;
  min-height: 250px;
}
.VideoWrapper p:first-child{
  flex-grow: 1;
  color: #0c76e4;
  font-size: 42px;
  font-weight: bold;
  
}
.VideoWrapper div{
  flex: 1;
  flex-basis: 400px;  

}
.videos {
  display: flex;
  flex-wrap: wrap;

}
.videos iframe{
  width:560px;
  height:315px;
}
.footer{
  padding: 30px;
  color: #7a7a7a;
  text-align: center;
}
.Menu{
  display: flex;
  width: 50%;
  margin: auto;

}
.Menu div{
  flex: 1;
  color: #7a7a7a;
  padding: 20px;
  text-align: left;
  font-weight: bold;
}

.Menu div a{
  text-decoration: none;
  color: #7a7a7a;
}
@media only screen and (max-width: 1024px) { 
  .dates{
    width: 100%;
  }
  .MainBody{
    
    flex-wrap: wrap;
  }
  .PublicationsWrapper .item{
    flex-wrap: wrap;
  }
  .item img{
    width: 100% !important;
  }
  .title{
    margin-top: 15px;
  }
  .PublicationsWrapper .links img{
    width: 20% !important;
  }
  .videos iframe{
    width: 100% !important;
    height: auto;
  }
}